<template>
  <div class="home">
    <el-table border :data="tradeList" size="small" stripe>
      <el-table-column align="center" header-align="center" label="标题" prop="title"/>
      <el-table-column align="center" header-align="center" label="链接" prop="link"/>
      <el-table-column align="center" header-align="center" label="订单号" prop="tradeNo" />
      <el-table-column align="center" header-align="center" label="日期" prop="date" />
      <el-table-column align="center" header-align="center" label="状态" prop="state">
        <template slot-scope="scope">
          {{scope.row.isOnline === 0 ? '下线' : '上线'}}
        </template>
      </el-table-column>
      <el-table-column align="center" header-align="center" label="审核">
        <template slot-scope="scope">
          <el-button size='small' type="primary" :disabled="scope.row.isOnline === 0"
                     @click="check(scope.row, scope.$index)">审核
          </el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
  export default {
    name: 'home',
    data() {
      return {
        tradeList: []
      }
    },
    mounted() {
      this.getTradeList()
    },
    methods: {
      getTradeList() {
        this.$axios.get('/online')
          .then(res => {
            this.tradeList = res.data
          })
      },
      check(row, index) {
        this.$confirm('确认下线吗？', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning',
          center: true
        }).then(() => {
          this.$axios.get('/online/update', {params: {tradeNo: row.tradeNo}})
            .then(res => {
              if (res.data.success) {
                this.$message({
                  type: 'success',
                  message: '下线成功!'
                });
                this.getTradeList()
              }
            })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消操作'
          });
        });
      }
    }
  }
</script>

<style scoped>
  .home {
    padding: 100px 30px 0;
  }
</style>
